body{
  margin: 0;
  padding: 0;
}
.app{
  min-height: 99vh;
  display: flex;
  flex-direction: column;
  padding: 2px;
}
.app-container {
  text-align: center;
  flex: 1;
}

button {
  font-size: 16px;
  padding: 10px 20px;
  margin-top: 20px;
  cursor: pointer;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 5px;
}

.birthday-decoration {
  font-size: 40px;
  margin-top: 20px;
}
.div-title{
  font-family: 'Almarai', sans-serif;
  font-size: 30px;
}
.title{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  margin-top: 30px;
}
.btn{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}
.words{
  font-family: 'Almarai', sans-serif;
  font-size: 20px;
  text-align:center;
  line-height: 2rem;
}
.words-container{
  margin-inline:15%;
  padding-bottom: 1rem;
}
footer{
  display: flex;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
}